iframe {
  display: none;
}

.App {
  font-family: "Noto Sans", sans-serif;
  box-sizing: border-box;
}

button {
  border: none;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
  letter-spacing: 0.8px;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.main {
  margin-block: 500px;
}